import React from "react"
import "normalize.css"
import "./illuminator.css"
import { graphql } from "gatsby"
import Modal from 'react-modal'
import thumbnail from "../components/thumbnail"

const customStyles = {
  content : {
    top                   : '50%',
    left                  : '50%',
    right                 : 'auto',
    bottom                : 'auto',
    marginRight           : '-50%',
    transform             : 'translate(-50%, -50%)'
  }
};

export default ({ data }) => {
  const [state,setState] = React.useState({modalIsOpen: false, modalText: ''});
  function openModal(text) {
    setState({modalIsOpen: true, modalText: text});
  }

  function afterOpenModal() {
    // references are now sync'd and can be accessed.
   // subtitle.style.color = '#f00';
  }

  function closeModal(){
    setState({modalIsOpen: false, modalText: ''});
  }

  return (
    <div>
      {
        data.allIlluminatorDataYaml.nodes.map((node, index) => {
          return <image name={'first.png'}></image> //<img key={index} onClick={() => openModal(node.text)} src={'../../' + node.image}/>
        })
      }
      <Modal
        isOpen={state.modalIsOpen}
        onAfterOpen={afterOpenModal}
        onRequestClose={closeModal}
        style={customStyles}
        contentLabel="Example Modal"
      >
        {state.modalText}
      </Modal>
    </div>
  )
}

export const query = graphql`
    query allIlluminator {
        allIlluminatorDataYaml {
            nodes {
                name
                image
                text
            }
        }
    }
`
